        .btn-white-xs.d-sm-none
            oc-play-button(:videoUrl='playVideoUrl' :isWhite='playIsWhite' role='button' aria-expanded='false' aria-controls='collapseSeeMore' )
    
    section.au-more
        .row
            .col-1
            #au-desc-mobile.col-10.text-justify.d-sm-none.font-weight-normal
