                p.text-right                
                    a.collapsed(href='#collapseSeeMore' data-toggle='collapse' role='button' aria-expanded='false' aria-controls="collapseSeeMore") {{ seeMoreText }}
            .col-1.d-sm-none
            .col-1.d-sm-none
            .col-10.text-justify
                 #collapseSeeMore.collapse.pb-5.pt-sm-5(v-html='seeMoreHtml')
            .col-1   
