            playVideoUrl: 'https://www.youtube.com/embed/7CdGJLmBubs',
            playIsWhite: true,
            htmlTextOne: 'ORTOGRAFIA CEREBRAL técnica única, original, la primera que no ayuda, ¡APOYA! a transformar tu vida de manera permanente, descubriendo la acción especifica que se grabó en tu cerebro en un momento específico de tu vida, que a pesar de taparlo el cerebro para protegerte, produce emociones y consecuencias que son tu vivir hasta el día de hoy o bien hasta que Dios te tenga en esta tierra, si así tu lo decides, está información te lleva a la repetición de hechos iguales o parecidos con una distancia especifica en años.<br><br>Esta técnica fue desarrollada y creada por la Dra. Mirna Juraidini, mujer de Mente inquieta que va del ingenio al pensamiento y del pensamiento a la creación con el deseo de servir y compartir adelantos en la ciencia y humanidad, para alojar en el interior de cada individuo la unión del  Cerebro y Corazón, Mente y Espiritualidad, para encontrar el centro de su verdad, que es única e individual, sin tener que seguir pagando nada que no te corresponde e iniciar a circular por la vida sin cargas, culpas, inseguridades etc. Y vivir para lo que fuiste destinado VIVIR EN PAZ Y ARMONIA, ¡ENCUADRADO CEREBRALMENTE!',
            seeMoreText: 'Conoce más ...',
            seeMoreHtml: '<p><img class="img-award rounded float-right" src="http://www.ortografiacerebral.com/wp-content/uploads/award.jpg" alt="Dra. Mirna Juraidini">Nadie te puede decir que hacer, ¡Solo TU! Puedes hacerlo, ya que son tus emociones, sentimientos, sensaciones, etc. Pero debes conocer, descubrir, reconocer y aceptar lo que en verdad viviste y escribiste en tu cerebro, Ortografía Cerebral es una dirección asistida para que encuentres esa información que te ha llevado a tener y vivir hasta este momento. Si quieres transformar estos resultados Ven a descubrir tu verdadera historia. Incorpora tu información consciente con tu información inconsciente, vive desahogado(a), Reduce el desgaste que te provocas, Administra tú energía, Sé Independiente, sin dejar de saber que necesitas un Complemento, aprendiendo a escoger lo ADECUADO, tanto en lo laboral, social, económico y emocional.</p><p>La alineación de tus cuadrantes cerebrales que son la base de esta técnica, te permiten o llevan a sanear y limpiar, enseñando “cómo” transformar tus pensamientos, logrando una mayor claridad de lo que en verdad quieres y necesitas, desarrollando tu ACTITUD Y APTITUD logrando como resultado ALTITUD Y ÉXITO, evitando caer de nuevo en perdida de tiempo, vida y dinero saliendo siempre victorioso de cualquier situación o acción ya que sabrás que el Creador y tu son UNO, EL contigo tú con EL.</p><p>Compruébate a ti mismo que no requieres de marcas, modelos ni novedades, pero que si te proporcionan seguridad, ésto es válido para Ti. Que el estar Bien o Mal tu lo creas, Hacer o no Hacer, Tener o no Tener, Ser o no Ser, por igual. Logra un mayor desarrollo, permítete tener el mando de tu vida, deja de sentirte un perdedor, has una nueva edición de ti mismo, todo se puede transformar, solo es que !TU lo quieras!, y que no permitas que tus ganancias inconscientes te sigan deteniendo, los limites TUS los pones deja de compartir tus pérdidas y fracasos, eso hace que crezcan, cambia tu forma de actuar, recuerda... ”SI SIGUES HACIENDO LO QUE HASTA AHORA HAS HECHO NO LLEGARAS MAS LEJOS DE DONDE ESTAS”</p><p>Ponte metas aunque sean desconocidos los caminos que tienes que recorrer, siempre hay una primera vez, un primer paso inicia una carrera, un viaje, una escalera. Te invito a ser el piloto de tu vehículo, deja de ser copiloto, Sé el mejor de los autos y solo permite que te acompañen las personas adecuadas y convenientes, TU tienes el control solo aprende a conducir...TÉ adecuadamente, todo lo que has probado en nada se parece a esta técnica Nueva,..... Creada por una Orgullosa MEXICANA, mujer que fue escogida por el creador para desarrollar las habilidades de y para un vivir pleno y feliz. Su lema “Servir y Ser sin dejar de Hacer para Tener “realizando su trabajo con honestidad y dirección celestial para llegar a su SER.</p>'
        }
    }
}
